* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  a {
    text-decoration: none;
    color: black;
  }
  
  /* .container {
      width: 1660px;
      margin: 0 auto;
  } */
  
  li {
    list-style: none;
  }
  
  @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');
  
  /* font-family: 'Noto Sans KR', sans-serif; */
  
  body {
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 500;
  }
  
  /***********search box****************/
  
  .search_box {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(15px);
    position: fixed;
    top: 0px;
    z-index: 99999;
    display: none;
  
    .search_box_wrap {
      width: 100%;
      background-color: #fff;
      padding: 30px 0;
    }
  
    .search_input {
      width: 100%;
      position: relative;
      padding-top: 30px;
  
      .search_icon {
        width: 50px;
        height: 50px;
        font-size: 35px;
        position: absolute;
        top: 35px;
        left: 25px;
      }
  
      input {
        width: 90%;
        height: 60px;
        margin: 0 auto;
        display: block;
        border-radius: 30px;
        outline: none;
        border: 2px solid #ccc;
        background-color: #eee;
        font-size: 20px;
        padding: 0 15px;
        text-align: center;
  
        &[placeholder] {
          font-weight: 700;
        }
      }
  
      .search_close {
        width: 50px;
        height: 50px;
        font-size: 30px;
        position: absolute;
        right: 0;
        top: 35px;
        cursor: pointer;
      }
    }
  
    .search_list_wrap {
      display: flex;
      margin-top: 30px;
  
      .search_list {
        width: 33.3333%;
        border-right: 1px solid #ccc;
        padding-left: 100px;
  
        &:last-child {
          border-right: 0;
        }
  
        h4 {
          font-size: 20px;
          margin: 30px 0;
        }
  
        li {
          margin-bottom: 15px;
        }
      }
  
      .recent_search {
        li:first-child {
          color: #888;
        }
  
        .delete_history span {
          font-size: 14px;
          color: #888;
          text-decoration: underline;
          display: block;
          margin-top: 50px;
          cursor: pointer;
        }
      }
    }
  }
  
  /*************header*************/
  
  header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 999;
    height: 70px;
  
    &:hover {
      background-color: #fff;
  
      a {
        color: black !important;
      }
    }
  }
  
  .head_up {
    position: absolute;
  }
  
  .head_down {
    position: fixed;
  }
  
  .header_wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .head_logo {
    width: 33.3333%;
  
    .imgwrap {
      width: 90px;
      margin-left: 25px;
      margin-top: 10px;
  
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  
  .head_lnb {
    width: 33.3333%;
  
    ul {
      display: flex;
      justify-content: space-around;
      line-height: 70px;
  
      li {
        font-size: 20px;
        font-weight: bold;
  
        &:nth-child(4) {
          position: relative;
  
          &::before {
            content: "";
            display: block;
            width: 1px;
            height: 14px;
            background-color: #ccc;
            position: absolute;
            top: 25px;
            left: -35px;
          }
        }
  
        a {
          display: block;
          width: 100%;
          color: #fff;
          position: relative;
          transition: .3s;
  
          &:hover {
            color: #002053;
  
            &::after, &::before {
              width: 50%;
            }
          }
  
          &::after {
            display: block;
            content: "";
            width: 0%;
            height: 2px;
            background-color: #002053;
            position: absolute;
            bottom: 13px;
            right: 50%;
            transition: .3s;
          }
  
          &::before {
            display: block;
            content: "";
            width: 0%;
            height: 2px;
            background-color: #002053;
            position: absolute;
            bottom: 13px;
            left: 50%;
            transition: .3s;
          }
        }
      }
    }
  }
  
  .head_gnb {
    width: 33.3333%;
  
    ul {
      display: flex;
      justify-content: flex-end;
  
      li {
        margin-right: 25px;
        position: relative;
  
        &:nth-child(4) {}
  
        .count {
          width: 20px;
          height: 20px;
          line-height: 20px;
          display: block;
          background-color: #002053;
          border-radius: 50%;
          text-align: center;
          color: #fff;
          font-size: 13px;
          position: absolute;
          top: 10px;
          right: -5px;
        }
  
        a {
          width: 100%;
          display: block;
          font-size: 25px;
          color: #fff;
          transition: .3s;
        }
      }
    }
  }
  
  .header:hover {
    .head_lnb > ul > li > a, .head_gnb > ul > li > a {
      color: black;
    }
  }
  
  /********drop down menu*********/
  
  .dropdown_menu {
    width: 100%;
    position: absolute;
    top: 70px;
    z-index: 100;
    background-color: #fff;
    display: none;
    padding: 20px;
    border-bottom: 2px solid #ccc;
  }
  
  .drop_menu_wrap {
    width: 100%;
    display: flex;
  }
  
  .drop_kids .dbox {
    width: 20%;
  
    &:last-child {
      border: 0;
    }
  }
  
  .dbox {
    width: 16.66667%;
    border-right: 1px solid #ccc;
    padding-left: 20px;
    padding-top: 10px;
  
    h4 {
      font-size: 22px;
      margin-bottom: 25px;
    }
  }
  
  .dropbox_06 {
    border-right: 0;
  }
  
  .menu_list {
    width: 100%;
  
    li {
      height: 20px;
      margin-bottom: 20px;
  
      a {
        &:hover {
          font-weight: bold;
          color: red !important;
        }
  
        display: block;
        width: 100%;
      }
    }
  }
  
  .sub_menu {
    margin-top: 50px;
  
    li {
      margin-bottom: 20px;
      font-weight: bold;
      height: 20px;
  
      a {
        display: block;
        width: 100%;
        position: relative;
      }
  
      &:hover a {
        color: red !important;
      }
    }
  }
  
  .drop_brand .drop_menu_wrap .brand_box {
    width: calc(100% - 80% - 24px);
    margin: 50px 20px;
  
    .brand_img {
      width: 100%;
      overflow: hidden;
      font-size: 0;
  
      img {
        width: 100%;
        height: auto;
        transform: scale(1.15);
        transition: .5s;
      }
  
      &:hover img {
        transform: scale(1);
      }
    }
  
    .brand_tit {
      text-align: center;
      margin-top: 20px;
      font-size: 20px;
    }
  }
  
  /***********section01*******/
  
  .section01 {
    width: 100%;
    height: 100vh;
  }
  
  .section01_cont {
    position: relative;
  }
  
  .section01 {
    .main_img {
      font-size: 0;
  
      img {
        width: 100%;
        height: 100vh;
      }
    }
  
    .main_logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  
    .main_tit {
      position: absolute;
      top: 20%;
      left: 50%;
      transform: translateX(-50%);
  
      h1 {
        color: #fff;
        font-size: 50px;
      }
    }
  
    .main_desc {
      width: 100%;
      position: absolute;
      bottom: 100px;
      left: 50%;
      transform: translateX(-50%);
  
      p {
        padding: 20px 0;
        font-size: 20px;
        text-align: center;
        color: #fff;
      }
  
      .report_box {
        width: 350px;
        height: 50px;
        border: 1px solid #ccc;
        margin: 0 auto;
        line-height: 50px;
        text-align: center;
        border-radius: 30px;
        transition: .3s;
  
        a {
          font-size: 20px;
          transition: .3s;
          color: #fff;
          display: block;
          width: 100%;
        }
  
        &:hover {
          background-color: #002053;
  
          a {
            color: #fff;
          }
        }
      }
    }
  }
  
  /****************section02******************/
  
  .section02 {
    width: 100%;
    height: 100vh;
    background: rgb(229, 222, 212);
  }
  
  .section02_cont {}
  
  .sec2_top {
    display: flex;
  
    /* align-items: center; */
    padding-top: 50px;
  
    .sec2_tit {
      width: 50%;
      padding-left: 50px;
  
      h4 {
        font-size: 40px;
      }
  
      p {
        color: #999;
        font-size: 18px;
      }
    }
  
    .sec2_desc {
      width: 50%;
  
      p {
        font-size: 20px;
      }
    }
  }
  
  .sec2_bot {
    width: 100%;
    padding: 0 20px;
    margin-top: 30px;
  
    ul {
      display: flex;
      justify-content: space-between;
  
      li {
        width: calc(100% - 75% - 50px);
  
        .bot_img {
          width: 100%;
          height: 400px;
  
          img {
            width: 100%;
            height: 100%;
          }
        }
  
        .bot_tit {
          font-size: 25px;
          margin-top: 10px;
        }
  
        .bot_desc {
          font-size: 18px;
          margin-top: 20px;
        }
  
        .bot_more {
          width: 50%;
          height: 45px;
          border: 1px solid #999;
          text-align: center;
          line-height: 45px;
          border-radius: 30px;
          margin: 0 auto;
          margin-top: 20px;
          cursor: pointer;
          transition: .3s;
  
          &:hover {
            background-color: #002053;
            color: #fff;
            border: 1px solid #002053;
          }
        }
      }
    }
  }
  
  /********section03*********/
  
  .section03 {
    width: 100%;
    height: 100vh;
  }
  
  .sec03_wrap {
    display: flex;
    padding: 0 20px;
    padding-top: 80px;
  }
  
  .sec03_L {
    width: 30%;
  
    .sec03_tit h4 {
      font-size: 36px;
      margin-top: 20px;
    }
  
    .sec03_tab {
      margin-top: 100px;
  
      li {
        margin-bottom: 10px;
  
        label::after {
          content: 'â†’';
          font-size: 24px;
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
        }
  
        input {
          display: none;
        }
  
        label {
          width: 300px;
          height: 70px;
          border-bottom: 1px solid black;
          display: block;
          font-size: 24px;
          line-height: 70px;
          position: relative;
          cursor: pointer;
          transition: .3s;
          border-radius: 5px;
          padding-left: 10px;
        }
      }
    }
  }
  
  #radio01:checked ~ #radio01_label, #radio02:checked ~ #radio02_label {
    box-shadow: 0 0 10px black;
  }
  
  .sec03_R {
    width: 70%;
    height: 800px;
    overflow: hidden;
  
    .swiper01 {
      width: 100%;
      height: 500px;
      padding: 0 20px;
      padding-top: 50px;
  
      /* overflow: hidden; */
      /* display: none; */
    }
  }
  
  /**********swiper01*********/
  
  .swiper01 .swiper-slide {
    .pdc_img {
      width: 100%;
      height: 500px;
  
      img {
        width: 100%;
        height: 100%;
      }
    }
  
    .category p {
      font-size: 12px;
      color: #999;
      margin-bottom: 10px;
    }
  
    .price {
      margin-top: 15px;
  
      p span {
        text-decoration: line-through;
        color: #ccc;
        padding-left: 10px;
  
        &:nth-child(2) {
          color: red;
          text-decoration: none;
          font-size: 14px;
        }
      }
    }
  
    .name p {
      font-size: 20px;
    }
  
    .buy_box {
      display: flex;
      flex-wrap: wrap;
      text-align: center;
      font-size: 25px;
      margin-top: 10px;
      color: #ccc;
  
      .like {
        width: calc(50% - 10px);
        transition: .3s;
  
        &:hover {
          color: red;
        }
      }
  
      .cart {
        width: calc(50% - 10px);
        transition: .3s;
  
        &:hover {
          color: #002053;
        }
      }
  
      .buy {
        width: 100%;
        font-size: 18px;
        margin-top: 10px;
        border: 1px solid #ccc;
        height: 45px;
        line-height: 45px;
        color: black;
        transition: .3s;
  
        &:hover {
          box-shadow: 0 0 10px black;
          background-color: #002053;
          color: #fff;
          border: 0;
        }
      }
    }
  }
  
