  /*****************section04*****************/
  
  .section04 {
    width: 100%;
    height: 100vh;
    border-top: 1px solid #ccc;
  }
  
  .section04_wrap {
    padding: 0 20px;
    display: flex;
    margin-top: 50px;
  }
  
  .sec04_2 {
    margin-top: 50px;
  }
  
  .section04_wrap {
    .sec04_L {
      width: 30%;
  
      .sec04_tit {
        margin-top: 100px;
  
        h4 {
          font-size: 36px;
        }
  
        p {
