  /*********footer********/
  
  footer {
    width: 100%;
    text-align: center;
    margin-top: 100px;
  }
  
  .footer_wrap {
    border-top: 1px solid #ccc;
    background-color: #eee;
    padding: 20px 10px;
  
    .footer_box1 {
      display: flex;
  
      .foot_logo {
        width: 20%;
  
        img {
          width: 90px;
          height: 30px;
        }
      }
  
      .foot_menu01 {
        width: 20%;
      }
  
      h4 {
        font-size: 20px;
        margin-bottom: 20px;
        cursor: pointer;
      }
  
      li {
        margin-bottom: 10px;
        cursor: pointer;
      }
  
      .foot_menu02, .foot_menu03, .foot_menu04 {
        width: 20%;
      }
    }
  
    .footer_box2 {
      margin-top: 50px;
  
      .foot_menu05 {
        display: flex;
  
        li {
          margin-bottom: 30px;
          cursor: pointer;
          font-weight: bold;
  
          &.red {
            color: red;
          }
  
          &.blue {
            color: blue;
          }
        }
      }
  
      .foot_menu05_1 {
        width: 20%;
  
        select {
          width: 45%;
          text-align: center;
          outline: none;
          background-color: #eee;
          border: 0;
          font-size: 16px;
  
          option:nth-child(1) {
            display: none;
          }
        }
      }
    }
  
    .footer_box3 {
      width: 100%;
      margin-top: 50px;
  
      .menu06 {
        display: flex;
  
        .menu06_1 {
          width: 50%;
          text-align: left;
          margin-left: 90px;
  
          .menu06_desc {
            p {}
  
            .foot_span01, .foot_span02 {
              text-decoration: underline;
              font-weight: bold;
              cursor: pointer;
            }
          }
        }
  
        .menu06_2 {
          width: 25%;
          text-align: left;
          padding-left: 50px;
  
          .cs_box {
            border: 1px solid black;
            text-align: center;
  
            /* width: 50%; */
            height: 35px;
            line-height: 35px;
            font-size: 22px;
            border-radius: 30px;
            margin-bottom: 20px;
          }
  
          h4 {
            margin-top: 10px;
            font-size: 20px;
          }
  
          p.day {
            font-size: 14px;
            margin-top: 10px;
          }
        }
  
        .menu06_3 {
          width: 25%;
  
          i {
            font-size: 35px;
            margin-left: 30px;
            cursor: pointer;
  
            &:nth-child(1) {
              margin-left: 0;
            }
          }
        }
      }
    }
  }
  
  .pagebtn {
    position: absolute;
    top: 0;
    right: 10px;
    z-index: 999;
  
    ul li {
      width: 50px;
      height: 50px;
      text-align: center;
      line-height: 50px;
      background-color: #fff;
      border-radius: 50%;
      margin-bottom: 10px;
      border: 1px solid #eee;
  
      a {
        width: 100%;
        height: 100%;
        display: block;
        transition: .3s;
      }
    }
  }
  
  .home {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fff;
    text-align: center;
  
    a {
      font-size: 25px;
      line-height: 50px;
    }
  }
  
  .on {
    background-color: #002053;
    color: #fff;
    border-radius: 50%;
  }
  